.bread-crumbs{display:flex;align-items:center;gap:8px;text-transform:capitalize;font-size:12px;max-width:1200px}.bread-crumbs__home-icon::before{content:"";font-family:"icomoon";font-size:16px;color:#313237;display:flex;align-items:center;justify-content:center}.bread-crumbs__arrow-icon::before{content:"";font-family:"icomoon";font-size:16px;color:#b4bdc3;display:flex;align-items:center;justify-content:center}.bread-crumbs__link{line-height:14px;padding:6px 0 4px;color:#313237}.bread-crumbs__current{line-height:14px;padding:6px 0 4px;color:#89939a}.truncate-text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}