.button__fav{border:1px solid #b4bdc3;background-color:#fff;width:40px;height:40px;flex-shrink:0;display:block;position:relative;cursor:pointer;font-family:"icomoon";transition:border-color .3s ease-in}.button__fav:hover{border-color:#313237}.button__fav::before{content:"";position:absolute;top:0;left:0;width:100%;text-align:center;line-height:40px;font-size:16px}.button__fav-filled{border:1px solid #b4bdc3;width:40px;height:40px;flex-shrink:0;display:flex;align-items:center;justify-content:center;font-family:"icomoon"}.button__fav-filled:hover{border-color:#313237}.button__fav-filled::before{content:"";color:#eb5757}