@import "../../styles/utils/variables";
@import "../../styles/utils/mixins";
@import "../../styles/utils/fonts";

.card {
  &__background {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;


        border: 1px solid $color-elements-lm;
        background: $color-white-lm;
    
        padding: $card-padding;
        gap: $card-gap;
        
        min-width: 212px;
        height: 439px;
        
        @include on-tablet {
            min-width: 237px;
            // max-width: 272px;
            height: 512px;
        }
        
        @include on-desktop {
            width: 272px;
            height: 506px;
        }

    }

    &__link {
        display: flex;
        justify-content: center;
    }

    &__img {
        max-width: 148px;
        max-block-size: 129px;

        @include on-tablet {
            max-width: 224px;
            max-height: 196px;
        }
        
        @include on-desktop {
            max-width: 208px;
            max-height: 196px;
        }
    }

  &__title {
    color: $color-primary-lm;
  }

  &__prices {
    display: flex;
    gap: 8px;
  }
  
  &__price {
    &::before {
      content: '$';
    }
  }
  
  &__fullprice {
    color: $color-secondary-lm;
    text-decoration-line: line-through;
    font-weight: 600;

    &::before {
      content: '$';
    }
  }

  &__divider {
    width: 95%;
    height: 1px;
    background-color: $color-elements-lm;
  }

  &__info {
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    flex-direction: row;

    &-name {
      color: $color-secondary-lm;
    }
  }

  &__buttons {
    display: flex;
    justify-content: space-between;
    gap: 8px;
  }
}
