.footer{width:100%;border-top:1px solid #e2e6e9}@media(min-width: 640px){.footer{height:96px}}.footer__content{padding:32px 16px;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;gap:32px}@media(min-width: 640px){.footer__content{max-width:1200px;margin:0 auto;padding:32px;flex-direction:row;justify-content:space-between;align-items:center;gap:0}}.footer__logo{flex:1;height:32px}.footer__nav{display:flex;flex-direction:column;align-items:flex-start;row-gap:16px}@media(min-width: 640px){.footer__nav{display:flex;flex:1;flex-direction:row;justify-content:space-between;align-items:center;margin:auto}.footer__nav:nth-child(2){justify-content:center}.footer__nav:last-child{justify-content:flex-end}}.footer__nav-link{text-decoration:none;font-size:12px;font-weight:700;line-height:11px;letter-spacing:.48px;text-transform:uppercase;color:#89939a;transition:all 200ms}.footer__back-to-top{display:flex;gap:16px;align-items:center;justify-content:center;align-self:stretch}@media(min-width: 640px){.footer__back-to-top{flex:1;justify-content:flex-end}}.footer__back-label{font-size:12px;font-weight:600;color:#89939a;cursor:pointer}.footer__back-button{width:32px;height:32px;border:1px solid #b4bdc3;display:flex;justify-content:center;align-items:center}.footer__back-button:hover{border-color:#313237}.footer__back-button:disabled{border-color:#b4bdc3}.footer__back-button:disabled:hover{border-color:#b4bdc3}.footer__back-button::before{content:"";font-family:"icomoon";font-size:16px;color:#313237;display:flex;align-items:center;justify-content:center;content:""}